<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <el-table :data="rightsList" border stripe>
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="authName" label=""></el-table-column>
        <el-table-column prop="path" label=""></el-table-column>
        <el-table-column prop="level" label="">
          <template v-slot:default="props">
            <el-tag v-if="props.row.level==='0'"></el-tag>
            <el-tag type="warning" v-else-if="props.row.level==='1'"></el-tag>
            <el-tag type="success" v-else></el-tag>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      rightsList: []
    }
  },
  created() {
    this.getRightList()
  },
  methods: {
    async getRightList() {
      const { data: res } = await this.$http.get('rights/list')
      if (res.meta.status !== 200) {
        return this.$Message.error('')
      }
      this.rightsList = res.data
    }
  }
}
</script>
