<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <el-row :gutter="20">
        <el-col :span="9">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getUserList">
            <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="3">
          <el-button type="primary" @click="dialogVisible=true"></el-button>
        </el-col>
      </el-row>
      <el-table :data="userlist" stripe border>
        <el-table-column type="index" label=""></el-table-column>
        <el-table-column label="" prop="username"></el-table-column>
        <el-table-column label="" prop="email"></el-table-column>
        <el-table-column label="" prop="mobile"></el-table-column>
        <el-table-column label="" prop="role_name"></el-table-column>
        <el-table-column label="">
          <template v-slot:default="props">
            <el-switch
              v-model="props.row.mg_state"
              active-color="#13ce66"
              inactive-color="#ff4949"
              @change="changeStatus(props.row)"
            ></el-switch>
          </template>
        </el-table-column>
        <!--  -->
        <el-table-column label="" width="200px">
          <template v-slot:default="slotProps">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="showEditDialog(slotProps.row.id)"
            ></el-button>
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="removeUserById(slotProps.row.id)"
            ></el-button>
            <el-tooltip effect="dark" content="" placement="top" :enterable="false">
              <el-button
                size="mini"
                type="warning"
                icon="el-icon-setting"
                @click="showRoleDialog(slotProps.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      ></el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogVisible" width="50%" @close="dialogClose">
      <el-form :model="addForm" :rules="rules" ref="addFormRef" label-width="80px">
        <el-form-item label="" prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="addForm.password" show-password></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <template v-slot:footer>
        <span>
          <el-button @click="dialogVisible = false"> </el-button>
          <el-button type="primary" @click="addUser"> </el-button>
        </span>
      </template>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editDialogClose">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
        <el-form-item label="">
          <el-input v-model="editForm.username" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label="" prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editDialogInfo"> </el-button>
      </span>
    </el-dialog>
    <el-dialog title="" :visible.sync="roleDialogVisible" width="50%" @close="roleDialogClose">
      <span>
        <p>{{userInfo.username}}</p>
        <p>{{userInfo.role_name}}</p>
        <p>
          
          <el-select v-model="selectRole" placeholder="">
            <el-option
              v-for="item in roleList"
              :key="item.id"
              :label="item.roleName"
              :value="item.id"
            ></el-option>
          </el-select>
        </p>
      </span>
      <span slot="footer">
        <el-button @click="roleDialogVisible = false"> </el-button>
        <el-button type="primary" @click="saveRoleInfo"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 2
      },
      userlist: [],
      total: 0,
      dialogVisible: false,
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          {
            required: true,
            min: 3,
            max: 10,
            message: '3-10',
            trigger: 'blur'
          }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          {
            required: true,
            min: 6,
            max: 15,
            message: '6-15',
            trigger: 'blur'
          }
        ],
        email: [
          {
            required: true,
            type: 'email',
            message: '',
            trigger: 'blur'
          }
        ],
        mobile: [
          {
            required: true,
            pattern: /^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      editDialogVisible: false,
      // 
      editForm: {},
      editFormRules: {
        email: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            type: 'email',
            message: '',
            trigger: 'blur'
          }
        ],
        mobile: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            pattern: /^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9])\d{8}$/,
            message: '',
            trigger: 'blur'
          }
        ]
      },
      // 
      roleDialogVisible: false,
      // 
      userInfo: {},
      // 
      roleList: [],
      // 
      selectRole: ''
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    async getUserList() {
      const { data: res } = await this.$http.get('/users', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) {
        return this.$message.error('  ')
      }
      this.userlist = res.data.users
      this.total = res.data.total
    },
    handleCurrentChange(num) {
      this.queryInfo.pagenum = num
      this.getUserList()
    },
    handleSizeChange(size) {
      this.queryInfo.pagesize = size
      this.getUserList()
    },
    async changeStatus(obj) {
      const { data: res } = await this.$http.put(
        `users/${obj.id}/state/${obj.mg_state}`
      )
      if (res.meta.status !== 200) {
        obj.status = !obj.status
        return this.$Message.error('')
      }
      return this.$Message.success('')
    },
    dialogClose() {
      this.$refs['addFormRef'].resetFields()
    },
    addUser() {
      this.$refs['addFormRef'].validate(async (valid) => {
        if (!valid) {
          return
        }
        const { data: res } = await this.$http.post('users', this.addForm)
        if (res.meta.status != 201) {
          return this.$Message.error('')
        }
        this.dialogVisible = false
        this.$Message.success('')
        this.getUserList()
      })
    },
    // 
    async showEditDialog(id) {
      const { data: res } = await this.$http.get('users/' + id)
      if (res.meta.status != 200) {
        return this.$Message.error('')
      }
      this.editForm = res.data
      this.editDialogVisible = true
    },
    /*  */
    editDialogClose() {
      this.$refs['editFormRef'].resetFields()
    },
    editDialogInfo() {
      this.$refs['editFormRef'].validate(async (valid) => {
        if (!valid) {
          return
        }
        const { data: res } = await this.$http.put(
          'users/' + this.editForm.id,
          { email: this.editForm.email, mobile: this.editForm.mobile }
        )
        if (res.meta.status !== 200) {
          return this.$Message.error('')
        }
        // 
        this.editDialogVisible = false
        // 
        this.getUserList()
        // 
        this.$Message.success('')
      })
    },
    async removeUserById(id) {
      const confirmResult = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch((err) => err)
      if (confirmResult !== 'confirm') {
        return this.$Message.info('')
      }
      const { data: res } = await this.$http.delete('users/' + id)
      if (res.meta.status !== 200) {
        return this.$Message.error('')
      }
      this.$Message.success('')
      this.getUserList()
    },
    async showRoleDialog(role) {
      this.userInfo = role
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$Message.error('')
      }
      this.roleList = res.data

      this.roleDialogVisible = true
    },
    async saveRoleInfo() {
      if (!this.selectRole) {
        return this.$Message.error('')
      }
      const { data: res } = await this.$http.put(
        `users/${this.userInfo.id}/role`,
        {
          rid: this.selectRole
        }
      )
      if (res.meta.status !== 200) {
        return this.$Message.error('')
      }
      this.$Message.success('')
      this.getUserList()
      this.roleDialogVisible = false
    },
    roleDialogClose() {
      this.selectRole = ''
      this.userInfo = {}
    }
  }
}
</script>

<style lang="less" scoped>
</style>
