<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!-- -->
    <el-card>
      <!--  -->
      <el-row>
        <el-col>
          <el-button type="primary" @click="addDialogVisible=true"></el-button>
        </el-col>
      </el-row>

      <el-table :data="roleList" border stripe>
        <el-table-column type="expand">
          <template v-slot:default="props">
            <el-row
              v-for="(item,index) in props.row.children"
              :key="item.id"
              :class="['rowBotton',index==0 ? 'rowTop' :'','vcenter']"
            >
              <!--  -->
              <el-col :span="5">
                <el-tag closable @close="removeRightById(props.row,item.id)">{{item.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <el-row
                  :class="[i2==0?'':'rowTop','vcenter']"
                  v-for="(item2,i2) in item.children"
                  :key="item2.id"
                >
                  <el-col :span="6">
                    <el-tag
                      closable
                      @close="removeRightById(props.row,item2.id)"
                      type="success"
                    >{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag
                      type="warning"
                      v-for="(item3) in item2.children"
                      :key="item3.id"
                      closable
                      @close="removeRightById(props.row,item3.id)"
                    >{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column label="" prop="roleName"></el-table-column>
        <el-table-column label="" prop="roleDesc"></el-table-column>
        <el-table-column label="" width="300px">
          <template v-slot:default="props">
            <el-button
              size="mini"
              type="primary"
              icon="el-icon-edit"
              @click="showEditDialog(props.row.id)"
            ></el-button>
            <el-button
              size="mini"
              type="danger"
              icon="el-icon-delete"
              @click="deleteRoleById(props.row.id)"
            ></el-button>
            <el-button
              size="mini"
              type="warning"
              icon="el-icon-setting"
              @click="showRightDialog(props.row)"
            ></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editDialogClose">
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="100px">
        <el-form-item label="" prop="roleName">
          <el-input v-model="editForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="editForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="editDialogInfo"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClose">
      <el-form :model="addForm" :rules="editFormRules" ref="addFormRef" label-width="100px">
        <el-form-item label="" prop="roleName">
          <el-input v-model="addForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="roleDesc">
          <el-input v-model="addForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer">
        <el-button @click="addDialogVisible = false"> </el-button>
        <el-button type="primary" @click="addDialogInfo"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="rightDialogVisible"
      width="50%"
      @close="setRightDialogClose"
    >
      <el-tree
        :data="roleTreeList"
        :props="treeProps"
        show-checkbox
        default-expand-all
        node-key="id"
        :default-checked-keys="defKeys"
        ref="treeRef"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="rightDialogVisible = false"> </el-button>
        <el-button type="primary" @click="allotRights"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data() {
    return {
      roleList: [],
      editDialogVisible: false,
      editForm: {},
      editFormRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' },
          {
            required: true,
            min: 2,
            max: 10,
            message: '2-10',
            trigger: 'blur'
          }
        ],
        roleDesc: [
          {
            required: true,
            message: '',
            trigger: 'blur'
          },
          {
            required: true,
            min: 2,
            max: 25,
            message: '2-25',
            trigger: 'blur'
          }
        ]
      },
      addForm: {
        roleName: '',
        roleDesc: ''
      },
      addDialogVisible: false,
      rightDialogVisible: false,
      roleTreeList: [],
      treeProps: {
        children: 'children',
        label: 'authName'
      },
      defKeys: [],
      roleId: ''
    }
  },
  created() {
    this.getRoleList()
  },
  methods: {
    async getRoleList() {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$Message.error('')
      }
      this.roleList = res.data
    },
    async showEditDialog(id) {
      const { data: res } = await this.$http.get('roles/' + id)
      if (res.meta.status !== 200) {
        this.$Message.error('')
      }

      this.editForm = res.data

      this.editDialogVisible = true
    },
    editDialogClose() {
      this.$refs['editFormRef'].resetFields()
    },
    editDialogInfo() {
      this.$refs['editFormRef'].validate(async (valid) => {
        if (!valid) {
          return
        }
        const { data: res } = await this.$http.put(
          'roles/' + this.editForm.roleId,
          {
            roleName: this.editForm.roleName,
            roleDesc: this.editForm.roleDesc
          }
        )
        if (res.meta.status !== 200) {
          return this.$Message.error('')
        }
        // 
        this.editDialogVisible = false
        // 
        this.getRoleList()
        this.$Message.success('')
      })
    },
    // 
    async deleteRoleById(id) {
      const result = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch((err) => err)
      if (result !== 'confirm') {
        return this.$Message.info('')
      }
      const { data: res } = await this.$http.delete('roles/' + id)
      if (res.meta.status !== 200) {
        return this.$Message.error('')
      }
      this.getRoleList()
      this.$Message.success('')
    },
    addDialogClose() {
      this.$refs['addFormRef'].resetFields()
    },
    addDialogInfo() {
      this.$refs['addFormRef'].validate(async (valid) => {
        if (!valid) {
          return
        }
        const { data: res } = await this.$http.post('roles', this.addForm)
        if (res.meta.status !== 201) {
          return this.$Message.error('')
        }
        this.getRoleList()
        this.addDialogVisible = false
        this.$Message.success('')
      })
    },
    async removeRightById(role, rightId) {
      const result = await this.$confirm(
        ', ?',
        '',
        {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }
      ).catch((err) => err)
      if (result !== 'confirm') {
        return this.$Message.info('')
      }
      const { data: res } = await this.$http.delete(
        `roles/${role.id}/rights/${rightId}`
      )
      if (res.meta.status !== 200) {
        return this.$Message.error('')
      }
      role.children = res.data
    },
    async showRightDialog(node) {
      this.roleId = node.id
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) {
        return this.$Message.error('')
      }
      this.roleTreeList = res.data
      this.getChildrenId(node, this.defKeys)
      this.rightDialogVisible = true
    },
    // 
    getChildrenId(node, arry) {
      if (!node.children) {
        return arry.push(node.id)
      }
      node.children.forEach((item) => {
        this.getChildrenId(item, arry)
      })
    },
    setRightDialogClose() {
      this.defKeys = []
    },
    async allotRights() {
      const tree = this.$refs['treeRef']
      const treeId = [...tree.getCheckedKeys(), ...tree.getHalfCheckedKeys()]
      const idStr = treeId.join(',')

      const { data: res } = await this.$http.post(
        `roles/${this.roleId}/rights`,
        { rids: idStr }
      )
      if (res.meta.status !== 200) {
        return this.$Message.error('')
      }
      this.$Message.success('')

      this.getRoleList()
      this.rightDialogVisible = false
    }
  }
}
</script>
<style lang="less">
.el-tag {
  margin: 10px;
}
.rowTop {
  border-top: 1px solid #eee;
}
.rowBotton {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>
